.post-enter {
  transform: translate(-250px);
  }
.post-enter-active {
    transform: translate(0px);
    transition: all 1500ms ease-in;
  }
.post-exit {
    opacity: 1;
  }
.post-exit-active {
    transform: translate(-250px);
    transition: all 1500ms ease-in;
  }
  .post-enter {
    outline: 2px solid red;
  }
  .post-enter-active {
    outline: 2px solid green;
  }
  .page__wrapper {
    margin-top: 30px;
    display: flex;
  }

  .page {
    padding: 10px;
    border: 1px solid teal;
    cursor: pointer;
  }

  .page__current {
    border: 2px solid orange;
    font-weight: bold;
  }

  .navbar {
    height: 50px;
    width: 100vw;
    display: flex;
    align-items: center;
    padding: 0 15px;
    background: lightgreen;
  }

  .navbar__links {
    margin-left: auto;
  }
  
  .post__btns {
    display: flex;
  }